export default {
  name: 'CustomerDetail7',
  mixins: [commonMixin],
  data () {
    return {
      isLoading_1: false,
      currentTab: 0,
      customer: customer,
      loanList: [],
      selectedLoan: null,
      loanModalOpen: false
    }
  },
  components: { 
    Avatar, CustomerInfo, TabHeader, // basic component
    LoanModal 
  },
  computed: {
    ...mapGetters(['BaseUrl', 'UserInfo', 'CustInfo'])
  },
  watch: {
    "$route": function() {
      this.initCustomerDetail7()
    }
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    openTheLoanModal(item) {
      this.selectedLoan = item
      this.panelOpen('loanModalOpen')
    },
    initCustDetail7() {
      this.isLoading_1 = true
      queryHousingLoans(this.UserInfo.id, this.CustInfo.ID).then(result => {
        if(result.body && result.body.length > 0) {
          let bufferList = result.body.map(item => {
            return {
              address: item.GAGE_ADDR,  //GAGE_ADDR 擔保品地址
              account: item.LOAN_ID,  //LOAN_ID 貸款帳號
              checkAt: item.LOAN_DATE,  //LOAN_DATE 貸放日期
              status: item.LOAN_STATUS_NM,  //LOAN_STATUS_NM 契約效力
              amount: Number(item.LOAN_AMT),  //LOAN_AMT 貸款金額
              last: Number(item.SPR_AMT),  //SPR_AMT 本金餘額
              series: item.LOAN_PERIOD,  //LOAN_PERIOD 借款期數
              fireRisk: item.FIRE_STATUS,  //FIRE_STATUS 火險狀態
              nextPayAt: item.NEXT_PAYDT,  //NEXT_PAYDT 下期應繳日
              nextRate: item.RATE,  //RATE 下期利率
              nextFee: Number(item.DUTY_PAYMT),  //DUTY_PAYMT 下期應繳金額
              nextSeries: item.NEXT_PAY_TERM,  //NEXT_PAY_TERM 下期期數
              getintCode: item.GETINT_CODE, //收費代號
              intList: item.intList.length == 0 ? []: item.intList.map(listItem => {
                return {
                  series: listItem.PAY_TERM,  //PAY_TERM 期數
                  last: Number(listItem.LAST_PRINCIPAL),  //LAST_PRINCIPAL 繳款前本金餘額
                  shouldPayAt: listItem.DUTY_PAYDT,  //DUTY_PAYDT 應繳日期
                  payAt: listItem.REAL_PAYDT,  //REAL_PAYDT 實繳日期
                  rate: listItem.INT_RATE,  //INT_RATE 適用年利率
                  renge: Number(listItem.RENEGE), //違約金
                  delayInt: Number(listItem.DELAY_INT), //延滯息
                  interest: Number(listItem.INTEREST), //本期利息
                  principal: Number(listItem.PRINCIPAL),  //PRINCIPAL 本期本金
                  payment: listItem.PAY_TYPE_NM  //PAY_TYPE_NM 繳款方式
                }
              })
            }
          })
          let groupByAddress = new Map
          bufferList.forEach((item, index) => {
            if(groupByAddress.has(item.address)) {
              groupByAddress.get(item.address).push(item)
            } else {
              groupByAddress.set(item.address, [item])
            }
          })
          this.loanList = Array.from(groupByAddress.values()).map(addressGroup => {
            return {
              address: addressGroup[0].address,
              data: addressGroup
            }
          })
          this.isLoading_1 = false
        } else {
          this.loanList = []
          this.isLoading_1 = false
        }
      })
    }
  },
  beforeMount () {
    let routeNumber = (this.$route.name.split(/-/g)).reverse()
    this.currentTab = Number(routeNumber[0])
  },
  async mounted () {
    this.initCustDetail7()
  }
}
